"use client"

import { useEffect, useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { LocationCapture, TrackingService } from "@/lib/services/tracking-service"
import dynamic from "next/dynamic"

// Importar el mapa din谩micamente para evitar problemas de SSR
const LiveLocationMap = dynamic(
  () => import("@/components/map/live-location-map").then(mod => mod.LiveLocationMap),
  { 
    ssr: false,
    loading: () => (
      <div className="h-[400px] flex items-center justify-center bg-gray-900 rounded-lg">
        <div className="animate-spin rounded-full h-8 w-8 border-2 border-current" />
      </div>
    )
  }
)

export default function SharePage({
  params
}: {
  params: { id: string }
}) {
  const [capture, setCapture] = useState<LocationCapture | null>(null)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    async function loadCapture() {
      try {
        const captures = await TrackingService.getLocationCaptures()
        const found = captures.find(c => c.id === params.id)
        if (found) {
          setCapture(found)
        } else {
          setError("Ubicaci贸n no encontrada")
        }
      } catch (err) {
        setError("Error al cargar la ubicaci贸n")
      }
    }
    loadCapture()
  }, [params.id])

  if (error) {
    return (
      <div className="container mx-auto p-6">
        <Card className="bg-black/40 backdrop-blur-sm border-gray-800">
          <CardContent className="p-6">
            <div className="text-center text-red-400">{error}</div>
          </CardContent>
        </Card>
      </div>
    )
  }

  if (!capture) {
    return (
      <div className="container mx-auto p-6">
        <Card className="bg-black/40 backdrop-blur-sm border-gray-800">
          <CardContent className="p-6">
            <div className="text-center">Cargando ubicaci贸n...</div>
          </CardContent>
        </Card>
      </div>
    )
  }

  return (
    <div className="container mx-auto p-6">
      <Card className="bg-black/40 backdrop-blur-sm border-gray-800">
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <span></span>
            <span>Ubicaci贸n Compartida</span>
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Mapa */}
          <div className="h-[400px] rounded-lg overflow-hidden">
            <LiveLocationMap
              height="400px"
              showAccuracyCircle={true}
              initialLocation={{
                latitude: capture.latitude,
                longitude: capture.longitude,
                accuracy: capture.accuracy
              }}
            />
          </div>

          {/* Detalles de la Ubicaci贸n */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-4">
              <div>
                <div className="text-sm font-medium text-muted-foreground">
                  Ubicaci贸n
                </div>
                <div className="text-lg">
                  {capture.city}, {capture.country}
                </div>
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <div className="text-sm font-medium text-muted-foreground">
                    Latitud
                  </div>
                  <div className="text-lg">
                    {capture.latitude.toFixed(6)}掳
                  </div>
                </div>
                <div>
                  <div className="text-sm font-medium text-muted-foreground">
                    Longitud
                  </div>
                  <div className="text-lg">
                    {capture.longitude.toFixed(6)}掳
                  </div>
                </div>
              </div>
              <div>
                <div className="text-sm font-medium text-muted-foreground">
                  Precisi贸n
                </div>
                <div className="text-lg">
                  {capture.accuracy.toFixed(1)}m
                </div>
              </div>
            </div>

            <div className="space-y-4">
              <div>
                <div className="text-sm font-medium text-muted-foreground">
                  Capturado
                </div>
                <div className="text-lg">
                  {new Date(capture.capturedAt).toLocaleString()}
                </div>
              </div>
              <div>
                <div className="text-sm font-medium text-muted-foreground">
                  Dispositivo
                </div>
                <div className="text-lg">
                  {capture.deviceInfo.browserInfo}
                </div>
                <div className="text-sm text-muted-foreground">
                  IP: {capture.deviceInfo.ipAddress}
                </div>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
